.home-tab {
  position: absolute;
  top: 2vw;
  left: 2vw;
}

.about-tab {
  position: absolute;
  top: 2vw;
  left: 7vw;
}

.projects-and-experience-tab {
  position: absolute;
  top: 2vw;
  left: 12vw
}

.particle-js {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
